import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { FaBars, FaTimes } from 'react-icons/fa';
import { FcNightPortrait } from 'react-icons/fc'
import { DropdownButton, Dropdown } from 'react-bootstrap';
import Login from './Pages/Login/Login';
import './Navbar.css';
import { useHistory } from 'react-router-dom';
import { logoutUser } from './UserSlice';
import { useDispatch } from 'react-redux';
import { toast } from 'react-toastify';
import Register from './Pages/Register/Register';
function Navbar() {
    const [check, setCheck] = useState(false);

    useEffect(() => {
        if (localStorage.getItem('token') !== null) setCheck(true);
        else setCheck(false);
    }, [localStorage.getItem('token')])

    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);
    const handleClick = () => setClick(!click);
    const showButton = () => {
        if (window.innerHeight <= 960) {
            setButton(false);
        } else {
            setButton(true);
        }
    }
    window.addEventListener('resize', showButton)
    //logout process
    const dispatch = useDispatch();
    const history = useHistory();
    const onLogout = () => {
        dispatch(logoutUser());
        toast.success('goodbye!', {
            position: toast.POSITION.BOTTOM_CENTER,
            autoClose: 1500
        });
        history.push('/');
        localStorage.removeItem('token');
        localStorage.removeItem('role');
        localStorage.removeItem('username');
        localStorage.removeItem('userId');
        localStorage.removeItem('userInformation');


    }
    return (
        <>
            <div className="navbar">
                <div className="navbar-container container">
                    <Link to='/home' className="navbar-logo">
                        <FcNightPortrait className='navbar-icon'></FcNightPortrait>
                        <h2>DOnlineJudge</h2>
                    </Link>
                    <div className="menu-icon" onClick={handleClick}>
                        {click ? <FaTimes /> : <FaBars />}
                    </div>
                    <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                        <li className="nav-item">
                            <Link to='/home' className='nav-links'> Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link to='/news' className='nav-links'> News</Link>
                        </li>
                        <li className="nav-item">
                            <Link to='/problem' className='nav-links'> Problem</Link>
                        </li>
                        <li className="nav-item">
                            <Link to='/status' className='nav-links'> Status</Link>
                        </li>
                        <li className="nav-item">
                            <Link to='/about' className='nav-links'> About</Link>
                        </li>
                        <li className="nav-btn ">
                            {!check ? (
                                    <Login/>
                            ) : <DropdownButton variant="secondary" title={localStorage.getItem('username')}>
                                    <Link to='/profile' className="dropdown-items">Settings</Link>
                                    <Dropdown.Item onClick={onLogout}>Logout</Dropdown.Item>
                                </DropdownButton>}
                        </li>
                        <li className="nav-btn">
                            {!check ? (
                                <Register />
                            ) : <></>}
                        </li>
                    </ul>
                </div>
            </div>
        </>
    )
}

export default Navbar
